import './MoreAbout.css';
import {motion} from 'framer-motion';
import image1 from '../../images/image1.jpg';
import image2 from '../../images/image2.jpg';
import image3 from '../../images/image3.jpg';
import image4 from '../../images/image4.jpg';
import image5 from '../../images/image5.JPG';
import image6 from '../../images/image6.jpg';

function MoreAbout() {
    const transition = {duration: 1, ease: [0.43, 0.13, 0.23, 0.96]}

    const scrollDown = {
        hidden: {
            y: "100vh",
            opacity: 0
        },
        visible: {
            y: "0"
        },
        exit: {
            y: "-100vh",
            opacity: 0
        }
    }

    const container = {
        hidden: { opacity: 1, scale: 0 },
        visible: {
          opacity: 1,
          scale: 1,
          transition: {
            delayChildren: 0.7,
            staggerChildren: 0.3
          }
        }
    }
        
    const item = {
        hidden: { y: 20, opacity: 0 },
        visible: {
          y: 0,
          opacity: 1
        }
    }

    return (
        <motion.div 
            variants={container}
            className="container"
            initial="hidden"
            animate="visible"
            exit="exit"
            transition={transition}>
            <div className="flexContainer">
                <div className="galleryContainer">
                    <motion.img 
                        variants={item}
                        className="gallery" src={image2} alt="" />
                    <motion.img
                        variants={item}
                        className="gallery" src={image1} alt="" />
                    <motion.img 
                        variants={item}
                        className="gallery" src={image3} alt="" />
                    <motion.img 
                        variants={item}
                        className="gallery" src={image4} alt="" />
                    <motion.img 
                        variants={item}
                        className="gallery" src={image5} alt="" />
                    <motion.img 
                        variants={item}
                        className="gallery" src={image6} alt="" />
                </div>
            </div>
          
        </motion.div>
    )
}

export default MoreAbout;
